}
.air-datepicker-nav--action path {
   fill: none;
   stroke: var(--adp-nav-arrow-color);
   stroke-width: 2px;
}
.air-datepicker-nav--title {
   border-radius: var(--adp-border-radius);
   padding: 0 8px;
   padding-bottom: 3px;
   font-weight: bold;
   font-size: 18px;
   color: $dark100;

}
.air-datepicker-nav--title i {
   font-style: normal;
   margin-left: 0.3em;
}
.air-datepicker-nav--title:hover {
   background: var(--adp-background-color-hover);
}
.air-datepicker-nav--title:active {
   background: var(--adp-background-color-active);
}
.air-datepicker-nav--title.-disabled- {
   cursor: default;
   background: none;
}

.air-datepicker-buttons {
   display: grid;
   grid-auto-columns: 1fr;
   grid-auto-flow: column;
   margin: -2px 16px 9px 20px;

}
.air-datepicker-button {
   padding: 0;
   display: inline-flex;
   color: var(--adp-btn-color);
   border-radius: var(--adp-btn-border-radius);
   height: var(--adp-btn-height);
   border: none;
   background: rgba(255, 255, 255, 0);
   &-accept {
      margin-left: 47px;
   }
}
.air-datepicker-button:hover {
   color: $dark25;
}
.air-datepicker-button:focus {
   color: $dark25;
   outline: none;
}
.air-datepicker-button:active {
   color: $dark25;
}
.air-datepicker-button span {
   font-family: "Montserrat", sans-serif;
   font-weight: 700;
   font-size: 12px;
   line-height: 15px;
   text-transform: uppercase;
   outline: none;
   display: flex;
   align-items: center;
   cursor: pointer;
   width: 100%;
   height: 100%;
}

.air-datepicker-time {
   display: grid;
   grid-template-columns: -webkit-max-content 1fr;
   grid-template-columns: max-content 1fr;
   grid-column-gap: 12px;
   align-items: center;
   position: relative;
   padding: 0 var(--adp-time-padding-inner);
}
.-only-timepicker- .air-datepicker-time {
   border-top: none;
}
.air-datepicker-time--current {
   display: flex;
   align-items: center;
   flex: 1;
   font-size: 14px;
   text-align: center;
}
.air-datepicker-time--current-colon {
   margin: 0 2px 3px;
   line-height: 1;
}
.air-datepicker-time--current-hours,
.air-datepicker-time--current-minutes {
   line-height: 1;
   font-size: 19px;
   font-family: "Century Gothic", CenturyGothic, AppleGothic, sans-serif;
   position: relative;
   z-index: 1;
}
.air-datepicker-time--current-hours:after,
.air-datepicker-time--current-minutes:after {
   content: "";
   background: var(--adp-background-color-hover);
   border-radius: var(--adp-border-radius);
   position: absolute;
   left: -2px;
   top: -3px;
   right: -2px;
   bottom: -2px;
   z-index: -1;
   opacity: 0;
}
.air-datepicker-time--current-hours.-focus-:after,
.air-datepicker-time--current-minutes.-focus-:after {
   opacity: 1;
}
.air-datepicker-time--current-ampm {
   text-transform: uppercase;
   align-self: flex-end;
   color: var(--adp-time-day-period-color);
   margin-left: 6px;
   font-size: 11px;
   margin-bottom: 1px;
}
.air-datepicker-time--row {
   display: flex;
   align-items: center;
   font-size: 11px;
   height: 17px;
   background: linear-gradient(to right, var(--adp-time-track-color), var(--adp-time-track-color)) left 50%/100%
      var(--adp-time-track-height) no-repeat;
}
.air-datepicker-time--row:first-child {
   margin-bottom: 4px;
}
.air-datepicker-time--row input[type="range"] {
   background: none;
   cursor: pointer;
   flex: 1;
   height: 100%;
   width: 100%;
   padding: 0;
   margin: 0;
   -webkit-appearance: none;
}
.air-datepicker-time--row input[type="range"]::-webkit-slider-thumb {
   -webkit-appearance: none;
}
.air-datepicker-time--row input[type="range"]::-ms-tooltip {
   display: none;
}
.air-datepicker-time--row input[type="range"]:hover::-webkit-slider-thumb {
   border-color: var(--adp-time-track-color-hover);
}
.air-datepicker-time--row input[type="range"]:hover::-moz-range-thumb {
   border-color: var(--adp-time-track-color-hover);
}
.air-datepicker-time--row input[type="range"]:hover::-ms-thumb {
   border-color: var(--adp-time-track-color-hover);
}
.air-datepicker-time--row input[type="range"]:focus {
   outline: none;
}
.air-datepicker-time--row input[type="range"]:focus::-webkit-slider-thumb {
   background: var(--adp-cell-background-color-selected);
   border-color: var(--adp-cell-background-color-selected);
}
.air-datepicker-time--row input[type="range"]:focus::-moz-range-thumb {
   background: var(--adp-cell-background-color-selected);
   border-color: var(--adp-cell-background-color-selected);
}
.air-datepicker-time--row input[type="range"]:focus::-ms-thumb {
   background: var(--adp-cell-background-color-selected);
   border-color: var(--adp-cell-background-color-selected);
}
.air-datepicker-time--row input[type="range"]::-webkit-slider-thumb {
   box-sizing: border-box;
   height: 12px;
   width: 12px;
   border-radius: 3px;
   border: 1px solid var(--adp-time-track-color);
   background: #fff;
   cursor: pointer;
   -webkit-transition: background var(--adp-transition-duration);
   transition: background var(--adp-transition-duration);
}
.air-datepicker-time--row input[type="range"]::-moz-range-thumb {
   box-sizing: border-box;
   height: 12px;
   width: 12px;
   border-radius: 3px;
   border: 1px solid var(--adp-time-track-color);
   background: #fff;
   cursor: pointer;
   -moz-transition: background var(--adp-transition-duration);
   transition: background var(--adp-transition-duration);
}
.air-datepicker-time--row input[type="range"]::-ms-thumb {
   box-sizing: border-box;
   height: 12px;
   width: 12px;
   border-radius: 3px;
   border: 1px solid var(--adp-time-track-color);
   background: #fff;
   cursor: pointer;
   -ms-transition: background var(--adp-transition-duration);
   transition: background var(--adp-transition-duration);
}
.air-datepicker-time--row input[type="range"]::-webkit-slider-thumb {
   margin-top: calc(var(--adp-time-thumb-size) / 2 * -1);
}
.air-datepicker-time--row input[type="range"]::-webkit-slider-runnable-track {
   border: none;
   height: var(--adp-time-track-height);
   cursor: pointer;
   color: transparent;
   background: transparent;
}
.air-datepicker-time--row input[type="range"]::-moz-range-track {
   border: none;
   height: var(--adp-time-track-height);
   cursor: pointer;
   color: transparent;
   background: transparent;
}
.air-datepicker-time--row input[type="range"]::-ms-track {
   border: none;
   height: var(--adp-time-track-height);
   cursor: pointer;
   color: transparent;
   background: transparent;
}
.air-datepicker-time--row input[type="range"]::-ms-fill-lower {
   background: transparent;
}
.air-datepicker-time--row input[type="range"]::-ms-fill-upper {
   background: transparent;
}

.air-datepicker {
   --adp-font-family: "Montserrat", sans-serif;
   --adp-font-size: 12px;
   --adp-width: 318px;
   --adp-z-index: 100;
   --adp-padding: 16px;
   --adp-grid-areas: "nav" "body" "buttons";
   --adp-transition-duration: 0.3s;
   --adp-transition-ease: ease-out;
   --adp-transition-offset: 8px;
   --adp-background-color: #fff;
   --adp-background-color-hover: #f0f0f0;
