   --adp-color-other-month-hover: #c5c5c5;
   --adp-border-color: #dbdbdb;
   --adp-border-color-inner: #efefef;
   --adp-border-radius: 4px;
   --adp-border-color-inline: #d7d7d7;
   --adp-nav-height: 28px;
   --adp-nav-arrow-color: var(--adp-accent-color);
   --adp-nav-action-size: 28px;
   --adp-nav-color-secondary: var(--adp-color-secondary);
   --adp-day-name-color: rgba(188, 156, 255, 1);
   --adp-day-name-color-hover: #8ad5f4;
   --adp-day-cell-width: 1fr;
   --adp-day-cell-height: 40px;
   --adp-month-cell-height: 42px;
   --adp-year-cell-height: 56px;
   --adp-pointer-size: 10px;
   --adp-poiner-border-radius: 2px;
   --adp-pointer-offset: 14px;
   --adp-cell-border-radius: 4px;
   --adp-cell-background-color-selected: rgba(188, 156, 255, 1);
   --adp-cell-background-color-selected-hover: rgba(188, 156, 255, 0.8);
