@import "../../vars.scss";

.user-feedback {
  display: flex;
  font-size: 14px;
  line-height: 24px;
  color: $dark75;
  &__left {
    & .like-btn {
      margin-right: 0;
      margin-left: 5px;
    }
  }
  &__avatar {
    margin-right: 10px;
    margin-bottom: 12px;
    filter: drop-shadow(0px 10px 20px rgba(31, 32, 65, 0.1));
  }
  &__username {
    font-weight: 700;
  }
  &__last-activity {
    font-weight: 400;
  }
  &__feedback {
    margin-top: 10px;
    max-width: 653px;
  }
}
