@import "../../vars.scss";

.type-block {
  display: flex;
  margin-bottom: 50px;
  &__tag {
    width: 37px;
    margin-right: 30px;
    text-align: right;
    color: $dark25;
    &_h1 {
      font-size: 24px;
      line-height: 29.26px;
    }
    &_h2 {
      font-size: 19px;
      line-height: 23.16px;
    }
    &_h3 {
      font-size: 12px;
      line-height: 14.63px;
    }
    &_body {
      font-size: 14px;
      line-height: 24px;
    }
  }
  &__element {
    max-width: 337px;
    color: $dark100;
    font-weight: 700;
    &_h1 {
      font-family: "Quicksand";
      font-size: 24px;
      line-height: 30px;
    }
    &_h2 {
      font-family: "Quicksand";
      font-size: 19px;
      line-height: 24px;
    }
    &_h3 {
      font-size: 12px;
      line-height: 15px;
      text-transform: uppercase;
    }
    &_body {
      max-width: 310px;
      font-size: 14px;
      line-height: 24px;
      color: $dark75;
      font-weight: 400;
    }
  }
}
